import infoBox from "../../components/boxstyle/infobox.vue"
import {
	parseQueryString
} from "../../../utils/query.js"

export default {
	data() {
		return {
			name: '',
			phone: '',
			id: ''
		}
	},

	methods: {
		clearPhone() {
			this.phone = '';
		},

		async submitInfo() {
			// 请求之前先判断一下
			if (this.name.length === 0 && this.phone.length !== 11) {
				setTimeout(()=>{
					uni.hideLoading({})
					uni.showModal({
						content: "请输入姓名和手机号"
					})
				},700)
				
			}
			
			uni.showLoading({});
			const params = {
				carId: Number(this.id),
				name: this.name,
				phone: this.phone
			}
			const info = parseQueryString(params);

			console.log(this.id)

			const opts = {
				url: "/sc/driverMng/bindDriver" + info,
				method: "post"
			}
			const res = await this.$http.httpTokenRequest(opts);
			console.log(res)
			
			if (res.data.code === 0) {
				uni.hideLoading({})
				uni.reLaunch({
					url: "/pages/home/home"
				})
			}
			// else {
			// 	uni.hideLoading({})
			// 	uni.showModal({
			// 		content: ""
			// 	})
			// }
		},

		onLoad(options) {
			this.id = options.id
		},

		components: {
			infoBox
		}
	}

}
